.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

html, body, div, span, h1, h2, h3, h4, h5, h6, p, em, img, strong, sub, sup, b, u, i, dl, dt, dd, ol, ul, li, fieldset, form, label, table, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, figcaption, figure, footer, header, hgroup, menu, nav, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  vertical-align: baseline;
  background: transparent;
  font-size: 100%;
  box-sizing: border-box; }

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  text-decoration: none;
  box-sizing: border-box; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

td, td img {
  vertical-align: top; }

input, select, button, textarea {
  margin: 0;
  padding: 0;
  font-size: 100%; }

input[type="checkbox"] {
  vertical-align: middle; }

input[type="radio"] {
  vertical-align: text-bottom; }

sub {
  vertical-align: sub;
  font-size: smaller; }

sup {
  vertical-align: super;
  font-size: smaller; }

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

html {
  height: 100% !important;
  overflow-y: scroll; }

body {
  font-family: Arial, sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 1.5;
  text-align: left;
  color: #092444;
  position: relative;
  width: 100%;
  height: 100%;
  background: #fff; }

body input:focus:required:invalid,
body textarea:focus:required:invalid {
  color: red; }

body input:required:valid,
body textarea:required:valid {
  color: green; }

input, select, button, textarea {
  font-family: inherit;
  outline: none;
  box-sizing: border-box; }

label, input[type="button"], input[type="submit"], button {
  cursor: pointer;
  outline: none; }

input[type='submit'] {
  -webkit-appearance: none;
  -webkit-border-radius: 0; }

a {
  color: #092444;
  text-decoration: none; }
  a:hover {
    text-decoration: none; }

a, input, button, a img, textarea {
  transition: color .3s ease, background .3s ease, opacity .3s ease, border .3s ease, box-shadow .3s ease;
  outline: none; }
  a:hover, a:focus, input:hover, input:focus, button:hover, button:focus, a img:hover, a img:focus, textarea:hover, textarea:focus {
    outline: none; }

img {
  width: auto;
  height: auto;
  max-width: 100%; }

.wrapper {
  margin: 0 auto;
  min-height: 100%;
  height: auto !important;
  height: 100%;
  position: relative;
  overflow: hidden; }

.textCenter {
  text-align: center; }

svg {
  max-height: 100%; }

